import { connect } from 'react-redux'
import Journey from '../components/Journey'
import { showJourney } from '../actions'

const mapDispatchToProps = (dispatch) => {
  return {
    openJourney: () => {
      const selected = 0; // HACK: hard-coded for now
      dispatch(showJourney(true, selected));
    },
    closeJourney: () => {
      dispatch(showJourney(false));
    },
    flyTo: (e, item, offset) => {
      const center = window.map.getCenter();
      const ptCenter = window.map.project(center);
      const centerOffset = window.map.unproject([ptCenter.x, ptCenter.y-offset]);
      window.map.easeTo({
        center: [item.loc[0], item.loc[1]-(centerOffset.lat - center.lat)]
      });
    },
  }
}

const mapStateToProps = (state) => {
  return {
    on: state.journey.on,
    route: state.journey.routes[state.journey.selected] || {},
    layout: state.layout,
  }
}

const JourneyFrame = connect(
                             mapStateToProps,
                             mapDispatchToProps
                             )(Journey)

export default JourneyFrame;



